#home {
  width: 100%;
  overflow-x: visible; }

.home-container {
  background: url("../../../images/retro-computers.jpg") no-repeat center center;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.75);
  background-blend-mode: multiply;
  margin: 0 auto;
  height: 100vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 20%;
  margin-bottom: 5rem; }

.tagline-container h2 {
  color: var(--tertiary);
  position: relative;
  text-align: left; }

.tagline-container h2:nth-child(1) {
  font-family: 'Lato', sans-serif;
  font-weight: 100;
  font-size: 2.4rem;
  letter-spacing: 0.1rem;
  bottom: -1rem; }

.tagline-container h2:nth-child(2),
.tagline-container h2:nth-child(3) {
  font-family: 'Lora', serif;
  font-size: 12.2rem;
  letter-spacing: 0.5rem; }

.tagline-container h2:nth-child(3) {
  top: -5.5rem; }

.tagline-container button {
  background: transparent;
  position: relative;
  color: white;
  border: none;
  border: 1px solid white;
  padding: 1rem;
  width: 125px;
  font-size: 2rem;
  border-radius: 0.5rem;
  transition: 0.25s; }

.tagline-container button:hover {
  transition: 0.25s;
  color: #111;
  background: rgba(255, 255, 255, 0.8); }

.tagline-container button:focus {
  outline: none; }

.tagline-container button:nth-child(5) {
  margin-left: 1rem; }

/*** Blinking Cursor on Home Page ***/
.react-rotating-text-cursor {
  -webkit-animation: blink 1.1s cubic-bezier(0.68, 0.01, 0.01, 0.99) infinite;
  animation: blink 1.1s cubic-bezier(0.68, 0.01, 0.01, 0.99) infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; }
  50% {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; }
  100% {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; } }

@keyframes blink {
  0% {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; }
  50% {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)"; }
  100% {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; } }
